* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
}

body, ul, h2, h3 {
  margin: 0;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  list-style: none;
}

.logo-link__image {
  display: flex;
}

.nav__list {
  align-items: center;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  display: flex;
}

.stars :not(:first-child) {
  margin-left: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background: url("star-active.edda4c43.svg") center no-repeat;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s, color .3s;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__tittle {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  margin-bottom: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-items: end;
  margin: 16px 0 24px;
  display: flex;
}

.card__reviews-number {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.card__product-price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__product-price-label {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__product-price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-link {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: color .3s, background-color .3s;
  display: flex;
}

.card__buy-link:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
}

.card:hover {
  transform: scale(1.2);
}

.cardhover .card__tittle {
  color: #34568b;
}

.catalog {
  --row-elements-number: 1;
  grid-template-columns: repeat(var(--row-elements-number), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --row-elements-number: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --row-elements-number: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --row-elements-number: 4;
  }
}
